import React, { Component } from 'react'
import { Navbar, Nav } from 'react-bootstrap'
import img from '../assets/Image/kgb.jpg'

export default class NavComp extends Component {
    render() {
        const bgColor = {
            backgroundColor: "#bd5d38"
        }
        return (            
            <div>
                <nav class="navbar navbar-expand-lg navbar-dark bg-primary fixed-top" id="sideNav">
                    <a class="navbar-brand js-scroll-trigger" href="#about">
                        <span class="d-block d-lg-none">Clarence Taylor</span>
                        <span class="d-none d-lg-block"><img class="img-fluid img-profile rounded-circle mx-auto mb-2" src={img} alt="" /></span>
                    </a>
                    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation"><span class="navbar-toggler-icon"></span></button>
                    <div class="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul class="navbar-nav">
                            <li class="nav-item"><a class="nav-link js-scroll-trigger" href="#about">About</a></li>
                            <li class="nav-item"><a class="nav-link js-scroll-trigger" href="#experience">Experience</a></li>
                            <li class="nav-item"><a class="nav-link js-scroll-trigger" href="#education">Education</a></li>
                            <li class="nav-item"><a class="nav-link js-scroll-trigger" href="#skills">Skills</a></li>
                            <li class="nav-item"><a class="nav-link js-scroll-trigger" href="#interests">Interests</a></li>
                            <li class="nav-item"><a class="nav-link js-scroll-trigger" href="#awards">Awards</a></li>
                        </ul>
                    </div>
                </nav>
            </div>
        )
    }
}
